<template>
    <Header></Header>
    <div class="map-wrap">
        <div ref="map" class="map-container" id="map"></div>
    </div>
    <div class="main-wrap">
        <div class="back-history-btn xicon-back-history" v-on:click="historyBack" v-wave></div>
        <div class="container">
            <div class="p-2">
                <div class="form-search">
                    <div class="form-group marker marker-a">
                        <input type="text" class="form-control" readonly v-model="addressFrom">
                    </div>
                    <div class="form-group marker marker-b">
                        <input type="text" class="form-control" readonly v-model="addressTo">
                    </div>
                </div>
                <div class="tariff-list">
                    <div class="tariff-wrap" v-wave v-for="item in tariffs" :key="item"
                        :class="item.id === tariff && 'active'" v-on:click="setTariff(item)">
                        <div class="tariff-icon">
                            <img :src="item.icon" :alt="item.name">
                        </div>
                        <span class="tariff-name">{{ item.name }}</span>
                        <b class="tariff-price">{{ calculateDistance(item) }} сом</b>
                    </div>
                </div>
            </div>
        </div>
        <div class="container order-footer-wrap">
            <div class="px-2">
                <div class="order-footer">
                    <div class="pay-method">
                        <span class="pay-method-button" v-on:click="openTab('payment')"></span>
                    </div>
                    <div><button class="button block blink-animate" v-wave v-on:click="createOrder">Заказать</button></div>
                    <div class="more-option">
                        <div class="more-option-button" v-on:click="openTab('comment')"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <vue-bottom-sheet ref="payment" :max-height="650">
        <div class="container">
            <div class="form-search-detail-wrap">
                <div class="form-search-detail form-search">
                    <div class="payment-method-list">
                        <div class="payment-item" v-wave :class="method == 1 && 'active'" v-on:click="setMehtod(1)">
                            <img src="../assets/img/icons/dollar-banknote.svg" alt="Cash">
                            <span>Наличка</span>    
                        </div>
                        <div class="payment-item disabled">
                            <img src="../assets/img/icons/mbank-v1.svg" alt="MBank">
                            <span>MBank</span>    
                        </div>
                    </div>
                    <button type="button" class="button block" v-wave v-on:click="closeTab('payment')">Готово</button>
                </div>
            </div>
        </div>
    </vue-bottom-sheet>
    <vue-bottom-sheet ref="comment" :max-height="650">
        <div class="container">
            <div class="form-search-detail-wrap">
                <div class="form-search-detail form-search">
                    <div class="form-group">
                        <textarea cols="10" class="form-control form-control-textarea" placeholder="Комментарий водителю" v-model="comment"></textarea>
                    </div>
                    <button type="button" class="button block" v-wave v-on:click="closeTab('comment')">Готово</button>
                </div>
            </div>
        </div>
    </vue-bottom-sheet>
</template>

<script>
import Header from '../components/Header'
import VueBottomSheet from '@webzlodimir/vue-bottom-sheet'
import "@webzlodimir/vue-bottom-sheet/dist/style.css"
import pointAIcon from '../assets/img/icons/dot-red'
import pointBIcon from '../assets/img/icons/dot-gray'
import mapboxgl from "mapbox-gl"
import 'mapbox-gl/dist/mapbox-gl.css'

export default {
    data() {
        return {
            map: '',
            tab: '',
            center: [74.603841, 42.876456],
            zoom: 11,
            addressFrom: '',
            addressTo: '',
            comment: '',
            tariff: 0,
            method: 1,
            pointAIcon: pointAIcon,
            pointBIcon: pointBIcon,
            distance: 0,
            price: 0,
        }
    },
    components: {
        Header,
        VueBottomSheet
    },
    created() {
        this.addressFrom = localStorage.getItem('address_a')
        this.addressTo = localStorage.getItem('address_b')
    },
    computed: {
        tariffs() {
            return (this.$store.getters.getTariffs)
                ? this.$store.getters.getTariffs
                : this.$store.dispatch('getTariffs')
        }
    },
    mounted() {
        if(localStorage.getItem('skey')) {
            mapboxgl.accessToken = localStorage.getItem('skey')
            this.map = new mapboxgl.Map({
                container: this.$refs.map,
                style: 'mapbox://styles/mapbox/streets-v12',
                minzoom: 5,
                center: this.center,
                zoom: 11
            })
            const start = String(localStorage.getItem('point_a')).split(',').reverse()
            const end = String(localStorage.getItem('point_b')).split(',').reverse()
            this.map.on('load', async () => {

                // set mapbox base url
                const baseUrl = 'https://api.mapbox.com/directions/v5/mapbox/driving/'

                // from address marker
                this.map.loadImage(
                    this.pointAIcon,
                    (error, image) => {
                        if (error) throw error
                        this.map.addImage('point_a', image)
                        this.map.addLayer({
                            id: 'point',
                            type: 'symbol',
                            layout: {
                                'icon-image': 'point_a',
                                'icon-size': 0.05,
                                'icon-anchor': 'center'
                            },
                            source: {
                                type: 'geojson',
                                data: {
                                    type: 'FeatureCollection',
                                    features: [
                                        {
                                            type: 'Feature',
                                            properties: {},
                                            geometry: {
                                                type: 'Point',
                                                coordinates: start
                                            }
                                        }
                                    ]
                                }
                            },
                        })
                    }
                )

                // to address marker
                this.map.loadImage(
                    this.pointBIcon,
                    (error, image) => {
                        if (error) throw error
                        this.map.addImage('point_b', image)
                        this.map.addLayer({
                            id: 'end',
                            type: 'symbol',
                            layout: {
                                'icon-image': 'point_b',
                                'icon-size': 0.05,
                                'icon-anchor': 'center'
                            },
                            source: {
                                type: 'geojson',
                                data: {
                                    type: 'FeatureCollection',
                                    features: [
                                        {
                                            type: 'Feature',
                                            properties: {},
                                            geometry: {
                                                type: 'Point',
                                                coordinates: end
                                            }
                                        }
                                    ]
                                }
                            },
                        })
                    }
                )

                // get route coordinates
                const params = new URLSearchParams({
                    steps: "true",
                    geometries: "geojson",
                    access_token: mapboxgl.accessToken,
                })
                const request = await fetch(baseUrl + `${start[0]},${start[1]};${end[0]},${end[1]}?` + params, { method: 'GET' })
                const response = await request.json()

                // set distance and calculate
                this.distance = response.routes[0].distance

                // create directions
                const geojson = {
                    type: 'Feature',
                    properties: {},
                    geometry: {
                        type: 'LineString',
                        coordinates: response.routes[0].geometry.coordinates
                    }
                }
                this.map.addLayer({
                    id: 'route',
                    type: 'line',
                    source: {
                        type: 'geojson',
                        data: geojson
                    },
                    layout: {
                        'line-join': 'round',
                        'line-cap': 'round'
                    },
                    paint: {
                        'fill-outline-color': '#fff',
                        'line-color': '#02C945',
                        'line-width': 6,
                        // 'line-opacity': 0.75
                    }
                })
            })
            this.map.fitBounds([
                start,
                end
            ])
            // this.map.setCenter([start[0], end[1]])
        }
    },
    methods: {
        open() {
            this.$refs.menu.open()
        },
        close() {
            this.$refs.menu.close()
        },
        historyBack() {
            this.$router.replace('/?openModal=true')
        },
        calculateDistance(item) {
            if (this.distance > 0) {
                if (this.tariff === 0 && item.default === true) {
                    this.tariff = item.id
                }
                let distance = (this.distance / 1000).toFixed(0)
                let price = (distance * item.perKm) + + item.planting
                return price
            }
        },
        setTariff(item) {
            this.tariff = item.id
        },
        setMehtod(method) {
            this.method = method
        },
        openTab(tab) {
            this.$refs[tab].open()
        },
        closeTab(tab) {
            this.$refs[tab].close()
        },
        createOrder() {
            const formData = {
                'point_a': localStorage.getItem('point_a'),
                'point_b': localStorage.getItem('point_b'),
                'address_a': this.addressFrom,
                'address_b': this.addressTo,
                'tariff': this.tariff,
                'method': this.method,
                'comment': this.comment,
                'distance': (this.distance > 0) ? (this.distance / 1000).toFixed(0) : 0,
            }
            this.$store.dispatch('createOrder', formData)
        }
    }
}
</script>