<template>
    <Header></Header>
    <MapBox :btn="locationButton" :loader="true"></MapBox>
    <div class="main-wrap">
        <div class="box-wrap">
            <h1 class="title text-center">Идет поиск машины</h1>
            <div class="progress-bar-wrap">
                <div class="progress-bar" :style="{ width: progressbar + '%' }"></div>
            </div>
            <div class="order-view-buttons">
                <button class="button-order-view xicon-cancel" v-wave v-on:click="modalStatus = true"></button>
                <button class="button-order-view xicon-detail" v-wave v-on:click="open"></button>
            </div>
        </div>
    </div>
    <div class="modal-overlay" v-if="modalStatus">
        <div class="modal-wrap">
            <div class="modal-header"></div>
            <div class="modal-body">
                <p class="text-v1 text-center pb-3">Вы дейстивтельно хотите отменить?</p>
            </div>
            <div class="modal-footer custom">
                <button type="button" class="button button-default block" v-wave v-on:click="modalStatus = false">Нет</button>
                <button type="button" class="button button-danger block" v-wave v-on:click="cancelOrder">Да</button>
            </div>
        </div>
    </div>
    <vue-bottom-sheet ref="menu">
        <div class="container">
            <h1 class="title text-center">Детали поездки</h1>
            <div class="detail-info-wrap">
                <div class="info-box">
                    <span>От куда</span>
                    <b>{{ detail.addressFrom }}</b>
                </div>
                <div class="info-box">
                    <span>Куда</span>
                    <b>{{ detail.addressTo }}</b>
                </div>
                <div class="info-box">
                    <span>Дата</span>
                    <b>{{ detail.created_at }}</b>
                </div>
                <div class="info-box">
                    <span>Сумма</span>
                    <b>{{ detail.amount }} сом</b>
                </div>
            </div>
        </div>
    </vue-bottom-sheet>
</template>
    
<script>
import Header from '../components/Header'
import MapBox from '../components/MapBox'
import VueBottomSheet from '@webzlodimir/vue-bottom-sheet'
import "@webzlodimir/vue-bottom-sheet/dist/style.css"

export default {
    data() {
        return {
            progressbar: 0,
            locationButton: false,
            modalStatus: false
        }
    },
    mounted() {
        const self = this
        setInterval(() => {
            self.progressbar = (self.progressbar + + 2 < 100) ? (self.progressbar + + 2) : 100
        }, 1000)
    },
    components: {
        Header,
        MapBox,
        VueBottomSheet
    },
    computed: {
        detail() {
            return this.$store.getters.getOrderDetail
        },
        loading() {
            return this.$store.getters.loading
        }
    },
    methods: {
        open() {
            this.$refs.menu.open()
        },
        close() {
            this.$refs.menu.close()
        },
        cancelOrder() {
            this.$store.dispatch('cancelOrder')
        }
    }
}
</script>