<template>
    <NavBar title="Активация аккаунта" />
    <a class="back-btn" v-on:click="historyBack" v-wave></a>
    <div class="container">
        <form class="form-wrap" @submit.prevent="activate" autocomplete="off">
            <div class="form-group">
                <label class="form-label">Введите код подтверждения</label>
                <input type="number" class="form-input" v-model="code">
            </div>
            <button type="submit" class="button block" :disabled="loading" v-wave>Подтвердить</button>
        </form>
    </div>
</template>

<script>
import NavBar from '../components/NavBar'

export default {
    data() {
        return {
            code: ''
        }
    },
    components: {
        NavBar
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
    },
    methods: {
        historyBack() {
            return history.back()
        },
        activate() {
            if (!this.code || String(this.code).trim() === "") {
                this.$store.dispatch('notification', { message: 'Введите код активации' })
            } else {
                const data = {
                    code: this.code,
                    phone: this.$router.currentRoute.value.params.phone
                }
                this.$store.dispatch('activation', data)
            }
        },
    }
}
</script>