<template>
  <Notification></Notification>
  <router-view></router-view>
</template>

<script>
import Notification from './components/Notification'

export default {
  components: {
    Notification
  },
  created() {
    const self = this
    setInterval(function () {
      if (localStorage.getItem('isAuth') && Math.round(Date.now() / 1000) > localStorage.getItem('expires')) {
        localStorage.clear()
        self.$router.push('/login?SessionExpired=true')
      }
      if (!navigator.onLine || window.addEventListener('offline', null)) {
        self.$store.dispatch('notification', { message: 'Отсутствует подключение к интернету' })
      }
    }, 3000)
  },
  mounted() {
    if (window.cordova && window.cordova.platformId === 'android') {
      const self = this
      var permissions = window.cordova.plugins.permissions
      permissions.hasPermission(permissions.ACCESS_COARSE_LOCATION, function (status) {
        if (!status.hasPermission) {
          permissions.requestPermission(permissions.ACCESS_COARSE_LOCATION,
            function (status) {
              if (status.hasPermission) {
                self.$router.go(self.$router.currentRoute)
              } else {
                self.$store.dispatch('notification', { message: 'Не удалось определить местоположение' })
              }
            },
            function () {
              self.$store.dispatch('notification', { message: 'Не удалось определить местоположение' })
            })
        }
      })
    }
  }
}
</script>