<template>
    <div class="map-wrap">
        <div v-if="loader" class="map-loader">
            <div class="lds-ripple">
                <div></div>
                <div></div>
            </div>
        </div>
        <div v-if="selected == 'a'" class="select-marker point-a" :class="markerActive && 'active'"></div>
        <div v-if="selected == 'b'" class="select-marker point-b" :class="markerActive && 'active'"></div>
        <div ref="map" class="map-container" id="map"></div>
    </div>
    <div v-if="btn" class="get-location-btn xicon-location" v-on:click="currentLocation" v-wave></div>
</template>

<script>
import mapboxgl from "mapbox-gl"

export default {
    data() {
        return {
            map: '',
            center: [74.603841, 42.876456],
            zoom: 11,
            markerActive: false
        }
    },
    props: {
        btn: Boolean,
        loader: Boolean
    },
    mounted() {
        if (localStorage.getItem('skey')) {
            mapboxgl.accessToken = localStorage.getItem('skey')
            this.map = new mapboxgl.Map({
                container: this.$refs.map,
                style: 'mapbox://styles/mapbox/streets-v12',
                minzoom: 5,
                center: this.center,
                zoom: 14
            })
            this.map.on("move", () => {
                this.center = this.map.getCenter()
            })
            this.map.on("drag", () => {
                this.markerActive = true
            })
            this.map.on("dragend", () => {
                this.markerActive = false
                this.setCoords()
            })
            this.map.on("zoom", () => {
                this.zoom = this.map.getZoom()
            })
            if (localStorage.getItem('point_a')) {
                this.map.setCenter(String(localStorage.getItem('point_a')).split(',').reverse())
            }
        }
    },
    computed: {
        selected() {
            return this.$store.getters.selected
        }
    },
    watch: {
        selected() {
            const coords = localStorage.getItem('point_' + this.$store.getters.selected)
            if (coords && coords !== undefined && coords !== "") {
                this.map.setZoom(14)
                this.map.setCenter(String(coords).split(',').reverse())
            }
        }
    },
    methods: {
        setCoords() {
            localStorage.setItem(('point_' + ((this.$store.getters.selected) ? this.$store.getters.selected : 'a')), (this.center.lat + ',' + this.center.lng))
            this.$store.dispatch('getAddressByCoord', { latitude: this.center.lat, longitude: this.center.lng })
        },
        currentLocation() {
            if ("geolocation" in navigator) {
                navigator.geolocation.getCurrentPosition((position) => {
                    const data = { latitude: position.coords.latitude, longitude: position.coords.longitude }
                    localStorage.setItem('point_a', (data.latitude + ',' + data.longitude))
                    localStorage.setItem('currLongitude', data.longitude)
                    localStorage.setItem('currLongitude', data.longitude)
                    this.$store.dispatch('getCurrentLocation', data)
                    this.map.setCenter([data.longitude, data.latitude])
                })
            }
        }
    }
}
</script>