import router from '../router'

export default {
    state: {
        suggest: '',
        selected: 'a',
        currentLocation: '',
        addressFrom: '',
        addressTo: '',
        mapCenter: '',
        tariffs: '',
        orderDetail: ''
    },
    mutations: {
        setSuggestList(state, payload) {
            state.suggest = payload.list
        },
        setSelected(state, payload) {
            state.selected = payload
        },
        setCurrentLocation(state, payload) {
            state.currentLocation = payload.location
        },
        setAddressByCoord(state, payload) {
            if(state.selected === 'a') {
                state.addressFrom = payload.location
            }
            if(state.selected === 'b') {
                state.addressTo = payload.location
            }
        },
        setTariffs(state, payload) {
            state.tariffs = payload.tariffs
        },
        setMapCenter(state, payload) {
            state.mapCenter = payload
        },
        setOrderDetail(state, payload) {
            state.orderDetail = payload.applicationDetail
        }
    },
    actions: {
        async getSuggestList({dispatch, commit}, searchText) {
            const data = {
                text: searchText,
                guid: localStorage.getItem('guid')
            }
            dispatch('sendRequest', {method: 'POST', endpoint: '/index/suggest', data})
            .then(response => response.json())
            .then(data => {
                commit('setSuggestList', data)
            })
        },
        async getCurrentLocation({dispatch, commit}, data) {
            dispatch('sendRequest', {endpoint: '/index/getCurrentLocation', data})
            .then(response => response.json())
            .then(data => {
                if(data.location !== '-') {
                    localStorage.setItem('address_a', data.location)
                    commit('setCurrentLocation', data)
                } else {
                    commit('notification', {message: 'Не удалось определить местоположение'})
                }
            })
        },
        async getAddressByCoord({dispatch, commit}, data) {
            dispatch('sendRequest', {endpoint: '/index/getCurrentLocation', data})
            .then(response => response.json())
            .then(data => {
                commit('setAddressByCoord', data)
            })
        },
        async getCoordByAddress({dispatch, state}, data) {
            dispatch('sendRequest', {endpoint: '/index/getCoordByAddress', data})
            .then(response => response.json())
            .then(data => {
                if(data.location !== '-' && data.location !== '') {
                    localStorage.setItem(('point_' + state.selected), data.location)
                }
            })
        },
        async getTariffs({dispatch, commit}) {
            dispatch('sendRequest', {method: 'GET', endpoint: '/index/tariffs'})
            .then(response => response.json())
            .then(data => {
                commit('setTariffs', data)
            })
        },
        async createOrder({dispatch, commit}, data) {
            dispatch('sendRequest', {endpoint: '/index/create-order', data})
            .then(response => response.json())
            .then(data => {
                if(data.response === true && data.applicationDetail.applicationId !== undefined && data.applicationDetail.applicationId !== '') {
                    localStorage.setItem('currentApplicationId', data.applicationDetail.applicationId)
                    commit('setOrderDetail', data)
                    router.replace('/view-order')
                }
            })
        },
        setSelected({commit}, step) {
            commit('setSelected', step)
        },
        setMapCenter({commit}, coords) {
            commit('setMapCenter', coords)
        }
    },
    getters: {
        suggest(state) {
            return state.suggest
        },
        selected(state) {
            return state.selected
        },
        mapCenter(state) {
            return state.mapCenter
        },
        currentLocation(state) {
            return state.currentLocation
        },
        getAddressFrom(state) {
            return state.addressFrom
        },
        getAddressTo(state) {
            return state.addressTo
        },
        getTariffs(state) {
            return state.tariffs
        },
        getOrderDetail(state) {
            return state.orderDetail
        }
    }
}