<template>
  <Header></Header>
  <MapBox :btn="true"></MapBox>
  <div class="main-wrap">
    <div class="container">
      <div class="form-search">
        <a class="form-button" v-wave="{ color: '#777', easing: 'ease-out', duration: 0.4, }" v-on:click="open">
          <span>Куда поедем?</span>
          <i class="fa-solid fa-arrow-right"></i>
        </a>
      </div>
    </div>
  </div>
  <vue-bottom-sheet ref="menu" :max-height="650">
    <div class="container">
      <div class="form-search-detail-wrap">
        <div class="form-search-detail form-search">
          <div class="form-group marker marker-a">
            <input type="text" class="form-control" ref="from" v-model="addressFrom" v-on:blur="getCoordByAddress('a')"
              placeholder="Откуда поедем">
            <span v-on:click="selected('a')" v-wave>Карта</span>
          </div>
          <div class="form-group marker marker-b">
            <input type="text" class="form-control" ref="to" v-model="addressTo" v-on:blur="getCoordByAddress('b')"
              placeholder="Куда поедем">
            <span v-on:click="selected('b')" v-wave>Карта</span>
          </div>
          <button type="button" class="button block" v-wave v-on:click="createOrder" :disabled="loading">Далее</button>
        </div>
        <!-- <div class="user-address-list">
          <div class="list-address-item" v-for="item in suggest.results" :key="item" v-wave>
            <b>{{ item.title.text }}</b>
            <span>{{ item.subtitle.text }}</span>
          </div>
        </div> -->
      </div>
    </div>
  </vue-bottom-sheet>
</template>
  
<script>
import Header from '../components/Header'
import MapBox from '../components/MapBox'
import VueBottomSheet from '@webzlodimir/vue-bottom-sheet'
import "@webzlodimir/vue-bottom-sheet/dist/style.css"

export default {
  data() {
    return {
      addressFrom: '',
      addressTo: '',
      suggestTextLength: 3,
      suggestList: '',
      searchText: ''
    }
  },
  components: {
    Header,
    MapBox,
    VueBottomSheet
  },
  created() {
    this.addressFrom = localStorage.getItem('address_a')
    this.addressTo = localStorage.getItem('address_b')
  },
  mounted() {
    if (this.$route.query['openModal']) {
      this.open()
    }
  },
  computed: {
    loading() {
      return this.$store.getters.loading
    },
    suggest() {
      return this.$store.getters.suggest
    },
    currentLocation() {
      return this.$store.getters.currentLocation
    },
    getAddressFrom() {
      return this.$store.getters.getAddressFrom
    },
    getAddressTo() {
      return this.$store.getters.getAddressTo
    }
  },
  watch: {
    getAddressFrom() {
      this.addressFrom = this.$store.getters.getAddressFrom
      localStorage.setItem('address_a', this.addressFrom)
      this.open()
    },
    getAddressTo() {
      this.addressTo = this.$store.getters.getAddressTo
      localStorage.setItem('address_b', this.addressTo)
      this.open()
    },
    // suggest() {
    //   const result = JSON.stringify(this.$store.getters.suggest)
    //   console.log(result.results)
    //   console.log(this.$store.getters.suggest.suggest_reqid)
    // },
    currentLocation() {
      return this.addressFrom = (this.currentLocation) ?? this.currentLocation
    }
  },
  methods: {
    open() {
      this.$refs.menu.open()
    },
    close() {
      this.$refs.menu.close()
    },
    createOrder() {
      if (!this.addressFrom || String(this.addressFrom).trim() === "") {
        this.$refs.from.focus()
        this.$store.dispatch('notification', { message: 'Введите адрес' })
      } else {
        if (!this.addressTo || String(this.addressTo).trim() === "") {
          this.$refs.to.focus()
          this.$store.dispatch('notification', { message: 'Введите адрес' })
        } else {
          this.$router.push('/order')
        }
      }
    },
    selected(step) {
      this.close()
      this.$store.dispatch('setSelected', step)
    },
    suggestRequest(text) {
      if (String(text).length > this.suggestTextLength) {
        this.suggestTextLength = this.suggestTextLength + 3
        this.$store.dispatch('getSuggestList', text)
      } else if (String(text).length < 3) {
        this.suggestTextLength = 3
      }
    },
    getCoordByAddress(step) {
      this.$store.dispatch('setSelected', step)
      this.searchText = String((step === 'a') ? this.addressFrom : this.addressTo).trim()
      if (this.searchText !== '' && this.searchText !== localStorage.getItem('address_' + step)) {
        localStorage.setItem(('address_' + step), this.searchText)
        this.$store.dispatch('getCoordByAddress', { text: this.searchText })
      }
    }
  }
}
</script>