<template>
    <NavBar title="Регистрация" />
    <a class="back-btn" v-on:click="historyBack" v-wave></a>
    <div class="container">
        <form class="form-wrap" @submit.prevent="createAccount" autocomplete="off">
            <div class="form-group">
                <label class="form-label">Имя</label>
                <input type="text" class="form-input" v-model="firstname">
            </div>
            <div class="form-group">
                <label class="form-label">Фамилия</label>
                <input type="text" class="form-input" v-model="lastname">
            </div>
            <div class="form-group">
                <label class="form-label">Номер телефона</label>
                <input type="tel" class="form-input" placeholder="Введите в формате 0700102030" v-model="phone">
            </div>
            <div class="form-group">
                <label class="form-label">Пароль</label>
                <input type="password" class="form-input" v-model="password">
            </div>
            <div class="form-group">
                <label class="form-label">Повторите пароль</label>
                <input type="password" class="form-input" v-model="passwordRepeat">
            </div>
            <p class="text-agree">Нажимая на кнопку “Регистрация” я принимаю условия <a href="https://xbt.kg/privacy-policy" target="_blank">Пользовательского соглашения</a></p>
            <button type="submit" class="button block" :disabled="loading" v-wave>Регистрация</button>
        </form>
    </div>
</template>

<script>
import NavBar from '../components/NavBar'

export default {
    data() {
        return {
            firstname: '',
            lastname: '',
            phone: '',
            password: '',
            passwordRepeat: ''
        }
    },
    components: {
        NavBar
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
    },
    methods: {
        historyBack() {
            return history.back()
        },
        createAccount() {
            if (!this.firstname || String(this.firstname).trim() === "") {
                this.$store.dispatch('notification', { message: 'Введите ваше имя' })
            } else {
                if (!this.lastname || String(this.lastname).trim() === "") {
                    this.$store.dispatch('notification', { message: 'Введите ваше фамилию' })
                } else {
                        if (!this.phone || String(this.phone).trim() === "") {
                            this.$store.dispatch('notification', { message: 'Введите номер телефона' })
                        } else {
                            if (String(this.phone).length !== 10) {
                            this.$store.dispatch('notification', { message: 'Неверный формат номера телефона' })
                        } else {
                            if (!this.password || String(this.password).trim() === "") {
                                this.$store.dispatch('notification', { message: 'Введите пароль' })
                            } else {
                                if (!this.passwordRepeat || String(this.passwordRepeat).trim() === "") {
                                    this.$store.dispatch('notification', { message: 'Повторите пароль' })
                                } else {
                                    if (this.password !== this.passwordRepeat) {
                                        this.$store.dispatch('notification', { message: 'Введенные пароли не совпадают' })
                                    } else {
                                        const formData = {
                                            "firstname": this.firstname,
                                            "lastname": this.lastname,
                                            "phone": this.phone,
                                            "password": this.password,
                                            "password_confirm": this.passwordRepeat
                                        }
                                        this.$store.dispatch('registration', formData)
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
</script>