export default {
    state: {
        personalInfo: '',
        skey: ''
    },
    mutations: {
        setPersonalInfo(state, payload) {
            state.personalInfo = payload.profile
        },
        setSKey(state, payload) {
            state.skey = payload
        }
    },
    actions: {
        async getPersonalInfo({dispatch, commit}) {
            dispatch('sendRequest', {endpoint: '/profile/personal'})
            .then(response => response.json())
            .then(data => {
                if(data && data.profile.guid) {
                    localStorage.setItem('guid', data.profile.guid)
                }
                commit('setPersonalInfo', data)
            })
        },
        
    },
    getters: {
        personalInfo(state) {
            return state.personalInfo
        },
        skey(state) {
            return state.skey
        }
    }
}