import router from '../router'

export default {
    state: {
        auth: (localStorage.isAuth && localStorage.token) ? localStorage.isAuth : false
    },
    mutations: {
        setAuth(state, payload) {
            state.auth = payload
        }
    },
    actions: {
        async auth({dispatch, commit}, formData) {
            dispatch('sendRequest', {endpoint: '/auth/login', data: formData})
            .then(response => response.json())
            .then(data => {
                if(data.response !== undefined && data.response === true) {
                    localStorage.expires = Math.round(Date.now() / 1000) + + 604800;
                    localStorage.isAuth = true
                    localStorage.token = data.token;
                    localStorage.skey = data.userKey;
                    commit('notification', {message: 'You have successfully logged in', type: 'success'})
                    router.replace('/')
                } else {
                    if(data.not_activated === true) {
                        router.push('/activation/' + formData.phone)
                    }
                    commit('notification', {message: data.messages})
                }
            })
        },
        async registration({dispatch, commit}, formData) {
            dispatch('sendRequest', {endpoint: '/auth/registration', data: formData})
            .then(response => response.json())
            .then(data => {
                if(data.response !== undefined && data.response === true) {
                    router.replace('/activation/' + formData.phone)
                } else {
                    commit('notification', {message: data.messages})
                }
            })
        },
        async activation({dispatch, commit}, formData) {
            dispatch('sendRequest', {endpoint: '/auth/activation', data: formData})
            .then(response => response.json())
            .then(data => {
                if(data.response !== undefined && data.response === true) {
                    router.replace('/login')
                    commit('notification', {message: 'Account successfully activated', type: 'success'})
                } else {
                    commit('notification', {message: data.messages})
                }
            })
        },
        async resendActivationCode({dispatch, commit}, formData) {
            dispatch('sendRequest', {endpoint: '/auth/activation/resend', data: formData})
            .then(response => response.json())
            .then(data => {
                if(data.response !== undefined && data.response === true) {
                    commit('notification', {message: data.messages, type: 'success'})
                } else {
                    commit('notification', {message: data.messages})
                }
            })
        },
        async restore({dispatch, commit}, formData) {
            dispatch('sendRequest', {endpoint: '/auth/restore', data: formData})
            .then(response => response.json())
            .then(data => {
                if(data.response !== undefined && data.response === true) {
                    commit('notification', {message: data.messages, type: 'success'})
                } else {
                    commit('notification', {message: data.messages})
                }
            })
        },
        logout() {
            localStorage.clear()
            router.replace('/login')
        }
    },
    getters: {
        isAuth(state) {
            return state.auth
        }
    }
}