<template>
    <NavBar title="Авторизация" />
    <div class="container">
        <form class="form-wrap" @submit.prevent="login" autocomplete="off">
            <div class="form-group">
                <label class="form-label">Номер телефона</label>
                <input type="tel" class="form-input" placeholder="Введите в формате 0700102030" v-model="phone">
            </div>
            <div class="form-group">
                <label class="form-label">Пароль</label>
                <input type="password" class="form-input" v-model="password">
            </div>
            <RouterLink to="/restore" class="restore-link">Забыли пароль?</RouterLink>
            <button type="submit" class="button block" :disabled="loading" v-wave>Войти</button>
            <p class="form-p pt-3">Нет аккаунта? <RouterLink to="/registration">Зарегистрироваться</RouterLink>
            </p>
        </form>
    </div>
</template>

<script>
import { RouterLink } from 'vue-router'
import NavBar from '../components/NavBar'

export default {
    data() {
        return {
            phone: '',
            password: '',
        }
    },
    components: {
        NavBar,
        RouterLink
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
    },
    methods: {
        login() {
            if (!this.phone || String(this.phone).trim() === "") {
                this.$store.dispatch('notification', { message: 'Введите номер телефона' })
            } else {
                if (!this.password || String(this.password).trim() === "") {
                    this.$store.dispatch('notification', { message: 'Введите пароль' })
                } else {
                    const formData = {
                        "phone": this.phone,
                        "password": this.password
                    }
                    this.$store.dispatch('auth', formData)
                }
            }
        }
    }
}
</script>