<template>
    <NavBar title="Настройки"></NavBar>
    <a class="back-btn" v-on:click="historyBack" v-wave></a>
    <div class="container">
        <form class="form-wrap" @submit.prevent="login" autocomplete="off">
            <div class="form-group">
                <label class="form-label">Имя</label>
                <input type="text" class="form-input" v-model="personalInfo.firstname">
            </div>
            <div class="form-group">
                <label class="form-label">Фамилия</label>
                <input type="text" class="form-input" v-model="personalInfo.lastname">
            </div>
            <div class="form-group">
                <label class="form-label">Номер телефона</label>
                <input type="text" class="form-input" v-model="personalInfo.phone" disabled>
            </div>
            <button type="submit" class="button block" :disabled="loading" v-wave>Сохранить</button>
        </form>
    </div>
</template>

<script>
import NavBar from '../components/NavBar'

export default {
    components: {
        NavBar
    },
    computed: {
        personalInfo() {
            return (this.$store.getters.personalInfo)
                ? this.$store.getters.personalInfo
                : this.$store.dispatch('getPersonalInfo')
        },
        orderList() {
            return (this.$store.getters.orderList)
                ? this.$store.getters.orderList
                : this.$store.dispatch('orderList')
        }
    },
    methods: {
        historyBack() {
            return history.back()
        }
    }
}
</script>