<template>
    <NavBar title="Восстановления пароля" />
    <a class="back-btn" v-on:click="historyBack" v-wave></a>
    <div class="container">
        <form class="form-wrap" @submit.prevent="activate" autocomplete="off">
            <div class="form-group">
                <label class="form-label">Номер телефона</label>
                <input type="tel" class="form-input" placeholder="Введите в формате 0700102030" v-model="phone">
            </div>
            <button type="submit" class="button block" :disabled="loading" v-wave>Далее</button>
        </form>
    </div>
</template>

<script>
import NavBar from '../components/NavBar'

export default {
    data() {
        return {
            phone: ''
        }
    },
    components: {
        NavBar
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
    },
    methods: {
        historyBack() {
            return history.back()
        },
        activate() {
            if (!this.phone || String(this.phone).trim() === "") {
                this.$store.dispatch('notification', { message: 'Введите номер телефона' })
            } else {
                if (String(this.phone).length !== 10) {
                    this.$store.dispatch('notification', { message: 'Неверный формат номера телефона' })
                } else {
                    const data = {
                        phone: this.phone
                    }
                    this.$store.dispatch('restore', data)
                }
            }
        },
    }
}
</script>