<template>
    <NavBar title="Мои заказы"></NavBar>
    <a class="back-btn" v-on:click="historyBack" v-wave></a>
    <div class="main-page-wrap">
        <div class="order-list-wrap">
            <tamplate v-for="item in orderList" :key="item">
                <span class="order-item-date">{{ item.created_at }}</span>
                <div class="order-item-wrap">
                    <div class="order-item-body">
                        <div class="order-item item-a">{{ item.addressFrom }}</div>
                        <div class="order-item item-b">{{ item.addressTo }}</div>
                    </div>
                    <div class="order-item-footer">
                        <span>Цена за поездку: <b>{{ item.amount }} сом</b></span>
                        <div>
                            <img src="../assets/img/icons/dollar-banknote.svg" alt="">
                        </div>
                    </div>
                </div>
            </tamplate>
        </div>
    </div>
</template>

<script>
import NavBar from '../components/NavBar'

export default {
    components: {
        NavBar
    },
    computed: {
        orderList() {
            return (this.$store.getters.orderList)
                ? this.$store.getters.orderList
                : this.$store.dispatch('orderList')
        }
    },
    methods: {
        historyBack() {
            return history.back()
        }
    }
}
</script>