<template>
    <div class="header">
        <div class="container">
            <div class="header-wrap">
                <button class="menu-button" v-on:click="open">
                    <span></span>
                    <span></span>
                    <span></span>
                </button>
                <div class="current-address" v-if="currentLocation">
                    <span>Ваш адрес</span>
                    <b>{{ currentLocation }}</b>
                </div>
            </div>
        </div>
    </div>
    <div class="left-bar-menu" :class="menuStatus && 'active'">
        <div class="user-info-wrap">
            <div class="user-info-photo">
                <img src="../assets/img/profile.png">
            </div>
            <div class="user-info-personal">
                <span v-if="personalInfo.firstname && personalInfo.lastname">{{ personalInfo.firstname + ' ' +
                    personalInfo.lastname }}</span>
                <b v-if="personalInfo.phone">{{ personalInfo.phone }}</b>
            </div>
        </div>
        <div>
            <div class="menu-wrap">
                <div class="menu-links">
                    <a href="#" class="payment-methods" v-wave>Способы оплаты</a>
                    <RouterLink to="/my-orders" v-wave>История заказов</RouterLink>
                    <a href="#" v-wave>Мои адреса</a>
                </div>
            </div>
            <div class="menu-wrap">
                <div class="menu-links">
                    <RouterLink to="/settings" v-wave>Настройки</RouterLink>
                    <a href="https://wa.me/+996703003995" target="_blank" v-wave>Служба поддержки</a>
                    <a href="#" v-wave>Работа в такси</a>
                </div>
            </div>
            <div class="menu-wrap">
                <div class="menu-links">
                    <a class="text-danger" v-on:click="logout" v-wave>Выйти из приложения</a>
                </div>
            </div>
        </div>
    </div>
    <div class="overlay" v-if="menuStatus" v-on:click="menuStatus = !menuStatus"></div>
</template>

<script>
export default {
    data() {
        return {
            menuStatus: false
        }
    },
    computed: {
        personalInfo() {
            return (this.$store.getters.personalInfo)
                ? this.$store.getters.personalInfo
                : this.$store.dispatch('getPersonalInfo')
        },
        currentLocation() {
            return this.$store.getters.currentLocation
        }
    },
    methods: {
        open() {
            this.menuStatus = true
        },
        close() {
            this.menuStatus = false
        },
        logout() {
            this.$store.dispatch('logout')
        }
    }
}
</script>