import router from '../router'

export default {
    state: {
        orderList: '',
    },
    mutations: {
        setOrdeList(state, payload) {
            state.orderList = payload.list
        }
    },
    actions: {
        async cancelOrder({dispatch}) {
            const applicationId = localStorage.getItem('currentApplicationId')
            dispatch('sendRequest', {endpoint: '/index/cancel/' + applicationId})
            .then(response => response.json())
            .then(data => {
                if(data.response === true) {
                    localStorage.removeItem('currentApplicationId')
                    router.replace('/')
                }
            })
        },
        async orderList({dispatch, commit}) {
            dispatch('sendRequest', {endpoint: '/index/orderList/'})
            .then(response => response.json())
            .then(data => {
                commit('setOrdeList', data)
            })
        }
    },
    getters: {
        orderList(state) {
            return state.orderList
        }
    }
}